import React, { useEffect, useState } from 'react';
import styled from '@emotion/styled';
import { ArrowLeft, ArrowRight } from 'react-feather';
import { projectInfo as projects } from '../../../data/personInfo/project';

import ProjectBox from '../ProjectBox/ProjectBox';

const ShowcaseWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 242px;
  overflow: hidden;
`;

const ArrowWrapper = styled.div`
  z-index: 20;
  position: absolute;
  display: flex;
  align-items: center;
  height: 100%;
  width: 5em;
  &:hover {
    cursor: pointer;
  }
`;

const ArrowLeftWrapper = styled(ArrowWrapper)`
  left: 0;
  right: auto;
`;
const ArrowRightWrapper = styled(ArrowWrapper)`
  right: 0;
  justify-content: flex-end;
  left: auto;
`;

const ListWrapper = styled.div`
  position: relative;
  padding: 20px;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
`;

const CardList = styled.div`
  position: relative;
  height: 100%;
  width: 300px;
  @media only screen and (max-width: 768px) {
    width: 12em;
  }
  @media only screen and (min-width: 960px) {
    width: 20em;
  }
`;

const Card = styled.div`
  /* 3d */
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: all 400ms ease-in-out;
  z-index: 10;
  transform: translate3d(0, 0, 0);
  &.current {
    z-index: 16;
    transform: translate3d(0, 0, 0);
  }
  &.prev,
  &.next {
    z-index: 15;
    opacity: 0.5;
  }
  &.prev {
    transform: translate3d(-33%, 0, 0);
    @media only screen and (min-width: 960px) {
      transform: translate3d(-26%, 0, 0);
    }
  }
  &.next {
    transform: translate3d(33%, 0, 0);
    @media only screen and (min-width: 960px) {
      transform: translate3d(26%, 0, 0);
    }
  }
`;

const Showcase = ({ items }) => {
  const [cur, setCur] = useState(0);
  useEffect(() => {
    let id = setInterval(() => {
      setCur((cur) => (cur + 1) % items.length);
    }, 5000);
    return () => {
      // reset when clicking
      clearInterval(id);
    };
  }, []);

  if (items && items.length > 0 && items.length < 4) {
    let i = 0;
    while (items.length < 4) {
      items.push(items[i++]);
    }
  }
  return (
    <ShowcaseWrapper>
      <ArrowLeftWrapper
        onClick={(e) => {
          e.preventDefault();
          setCur((cur - 1 + items.length) % items.length);
        }}
      >
        <ArrowLeft />
      </ArrowLeftWrapper>
      <ArrowRightWrapper
        onClick={(e) => {
          e.preventDefault();
          setCur((cur + 1) % items.length);
        }}
      >
        <ArrowRight />
      </ArrowRightWrapper>
      <ListWrapper>
        <CardList>
          {items &&
            items.map((el, i) => {
              let cls = '';
              if (i === cur) {
                cls = 'current';
              } else if (i === (cur - 1 + items.length) % items.length) {
                cls = 'prev';
              } else if (i === (cur + 1) % items.length) {
                cls = 'next';
              }
              return (
                <Card key={i} className={cls}>
                  <ProjectBox project={el} />
                </Card>
              );
            })}
        </CardList>
      </ListWrapper>
    </ShowcaseWrapper>
  );
};

export default () => <Showcase items={projects} />;
