/**@jsx jsx */
import { jsx } from 'theme-ui';
import React from 'react';
import styled from '@emotion/styled';

const ProjWrapper = styled.div`
  position: relative;
  border-radius: 15px;
  box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.15);
  height: 100%;
  width: 100%;
  padding: 1em;
  margin: 0.5em 0;
  overflow: hidden;
  transition: all ease-in 0.2s;
  .project--name {
    font-weight: 600;
    padding-bottom: 0.5em;
    z-index: -1;
  }
  .project--intro {
    font-size: 14px;
    z-index: -1;
  }
`;

const StyledCollapsible = styled.div`
  position: absolute;
  left: 0;
  z-index: 1;

  width: 100%;
  height: 100%;
  margin: 0.5em 0;
  padding: 1em;
  bottom: 100%;
  transition: bottom 200ms ease-in;
  &:before {
    margin: 0.5em 0;
    padding: 1em;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 15px;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.5);
    opacity: 1;
    z-index: -1;
  }
`;

const StyledBox = styled.div`
  position: relative;
  padding: 1em 0.5em;
  display: flex;
  height: 100%;
  width: 100%;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  &:hover {
    ${StyledCollapsible} {
      bottom: 0px;
    }
  }
`;

const StyledTag = styled.div`
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: start;
  > a {
    /* color: white; */
    display: inline-block;
    padding: 0 0.5em;
    margin-left: 0.5em;
    transition: color 200ms ease-in;
    &:hover {
      color: #f15a24;
    }
  }
`;

const ProjectBox = ({ project }) => {
  const { name, projectUrl, repoUrl, isOpenSource, projectIntro } = project;
  return (
    <StyledBox>
      <ProjWrapper
        sx={{
          backgroundColor: 'offwhite',
        }}
      >
        <div className="project--name">{name}</div>
        <div className="project--intro">{projectIntro}</div>
        <StyledCollapsible>
          <StyledTag>
            <a href={projectUrl} rel="noreferrer" target="_blank">
              Project Url
            </a>
            {isOpenSource && (
              <a target="_blank" rel="noreferrer" href={repoUrl}>
                Repo Url
              </a>
            )}
          </StyledTag>
        </StyledCollapsible>
      </ProjWrapper>
    </StyledBox>
  );
};

export default ProjectBox;
