import React from 'react';
import FloatText from '../animations/FloatText';
import styled from '@emotion/styled';

const ProgressBar = styled.div`
  height: 1.5rem;
  border-radius: 15px;
  position: relative;
  background: ${({ barColor }) => (barColor ? barColor : '#6194A7')};

  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  z-index: 0;
`;

const CurBarWrapper = styled.div`
  position: absolute;
  width: ${({ percent }) => percent + '%'};
  transition: width 200ms ease-in;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
`;

const CurrentProgression = styled.div`
  background: ${({ percent, finishedColor, progressColor }) => {
    if (percent != 100) {
      return progressColor ? progressColor : 'linear-gradient(to right,#ffd8cf,#d0505d)';
    } else {
      return finishedColor ? finishedColor : '#22BB9C';
    }
  }};
  border-radius: 15px;
  width: 100%;
  height: 100%;
  animation: expand 2s;
  @keyframes expand {
    0% {
      width: 0%;
    }
    100% {
      width: 100%;
    }
  }
`;

const ProrgressText = styled.div`
  position: absolute;
  color: ${({ txtColor }) => (txtColor ? txtColor : '#f7e9df')};
  font-size: 1em;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

export default ({
  barTxt,
  percent,
  txtColor,
  barColor,
  progressColor,
  finishedColor,
  isAnimated,
}) => {
  if (!percent) {
    percent = 0;
  }
  return (
    <ProgressBar barColor={barColor}>
      {isAnimated && barTxt && <FloatText str={barTxt}></FloatText>}
      {!isAnimated && barTxt && <ProrgressText txtColor={txtColor}>{barTxt}</ProrgressText>}
      <CurBarWrapper percent={percent}>
        <CurrentProgression
          percent={percent}
          progressColor={progressColor}
          finishedColor={finishedColor}
        />
      </CurBarWrapper>
    </ProgressBar>
  );
};
