import React, { useEffect, useState } from 'react';
import styled from '@emotion/styled';
import ProgressBar from '../components/ProgressBar/ProgressBar';
import goals from '../../data/personInfo/goals/2020';

const GoalItem = styled.div`
  position: relative;
`;

const GoalHeader = styled.div`
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  padding: 0 1em;
  justify-content: space-between;
`;

const GoalName = styled.span`
  font-size: 1em;
`;

const StyledTagWrapper = styled.div`
  display: inline;
  padding: 0 0.5em;
`;
const StyledTag = styled.span`
  background: grey;
  color: white;
  padding: 0 0.5em;
  border-radius: 8px;
  margin-left: 0.5em;
`;

const Goal = (props) => {
  const { type, name, startDate, endDate, isDone } = props;
  let percent = 0;
  if (isDone) {
    percent = 100;
  }
  if (type == 'Countdown') {
    let total = Date.parse(endDate) - Date.parse(startDate);
    let cur = Date.now() - Date.parse(startDate);
    let diff = Date.parse(endDate) - Date.now();
    let timeLeft = {
      days: Math.floor(diff / (1000 * 60 * 60 * 24)),
      hours: Math.floor(diff / (1000 * 60 * 60)) % 24,
      minutes: Math.floor(diff / (1000 * 60)) % 60,
      seconds: Math.floor(diff / 1000) % 60,
    };
    if (!isDone) {
      percent = getPercent(cur, total);
    }
    return (
      <GoalItem>
        <GoalHeader>
          <GoalName>{name}</GoalName>
          <StyledTagWrapper>
            {!isDone && timeLeft.days > 0 && <StyledTag>{timeLeft.days} days</StyledTag>}
            {!isDone && timeLeft.days < 0 && <StyledTag>Not finished</StyledTag>}
          </StyledTagWrapper>
        </GoalHeader>
        <ProgressBar barTxt={percent + '%'} percent={percent} />
      </GoalItem>
    );
  }
  return (
    <GoalItem>
      <GoalHeader>
        <GoalName>{name}</GoalName>
      </GoalHeader>
      <ProgressBar percent={percent} isAnimated={true} barTxt="🎯 Pursuing" />
    </GoalItem>
  );
};

const GoalProgress = ({ txt, percent, ...props }) => (
  <ProgressBar barTxt={txt} percent={percent} {...props} />
);

// utils
function getPercent(cur, total) {
  return Math.min(100, Math.max(0, Math.floor((100 * cur) / total)));
}

function getDayOfYear() {
  const oneDay = 1000 * 60 * 60 * 24;
  const current = new Date();
  const startDate = new Date(current.getFullYear(), 0, 0);
  const diff =
    current - startDate + (current.getTimezoneOffset() - startDate.getTimezoneOffset()) * 60 * 1000;
  return Math.floor(diff / oneDay);
}

function isLeapYear(year) {
  if (year % 4 != 0) {
    return false;
  }
  return year % 100 != 0 || year % 400 == 0;
}
const getYearPercent = () => {
  let d = new Date();
  let year = d.getFullYear();
  let total = 365;
  if (isLeapYear(year)) {
    total += 1;
  }
  let index = getDayOfYear();
  return getPercent(index, total);
};
const getDayPercent = () => {
  let start = new Date();
  start.setHours(8, 0, 0, 0);
  let end = new Date();
  end.setHours(21, 0, 0, 0);
  let cur = new Date();
  let diff = cur - start;
  let total = end - start;
  return getPercent(diff, total);
};
export default () => {
  const YearPercent = getYearPercent();
  const [clock, setClock] = useState({ DayPercent: getDayPercent() });

  useEffect(() => {
    // fire once per minute
    let id = setInterval(() => {
      setClock({ DayPercent: getDayPercent() });
    }, 60000);
    return () => {
      clearInterval(id);
    };
  }, []);
  return (
    <ul>
      <li>
        <GoalItem>
          <GoalHeader>
            <GoalName>⌛ Day Prgoress (8am - 9pm)</GoalName>
          </GoalHeader>
          <GoalProgress txt={clock.DayPercent + '%'} percent={clock.DayPercent} />
        </GoalItem>
      </li>
      <li>
        <GoalItem>
          <GoalHeader>
            <GoalName>📅 Year Prgoress</GoalName>
          </GoalHeader>
          <GoalProgress txt={YearPercent + '%'} percent={YearPercent} />
        </GoalItem>
      </li>
      {goals
        .filter((goal) => {
          return !goal.isHidden;
        })
        .map((goal) => {
          return (
            <li key={goal.name}>
              <Goal {...goal} />
            </li>
          );
        })}
    </ul>
  );
};
