import React from 'react';
import Layout from '../layout/Page';
import SEO from '../components/SEO/SEO';
import { StyledExternalLink } from '../components/CustomMDX/Link';
import CardBox from '../components/CardBox';
import YearGoals from '../templates/YearGoals';
import Showcase from '../components/Showcase';
import { graphql } from 'gatsby';

export default ({ data }) => {
  const { email } = data.site.siteMetadata.social;
  return (
    <Layout activeTag="about">
      <SEO title="About" isBlogPost={false} lang="en" />
      <section style={{ gridColumn: '3 / -3' }}>
        <section className="contact">
          <h2 style={{ fontSize: '2rem' }}>About</h2>
          <p>
            Say hi👋 to me by sending a email at{' '}
            <StyledExternalLink href={'mailto:' + email}>{email}</StyledExternalLink>. You can also
            find me on{' '}
            <StyledExternalLink href={'https://github.com/tizee'} target="__blank">
              github
            </StyledExternalLink>
            .
          </p>
        </section>
        <CardBox title="🎨 Projects">
          <Showcase />
        </CardBox>
        <CardBox title="📈 Goals">
          <YearGoals />
        </CardBox>
      </section>
    </Layout>
  );
};

export const query = graphql`
  query {
    site {
      siteMetadata {
        social {
          email
        }
      }
    }
  }
`;
